import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { Link } from 'react-router-dom';

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);

export default function NoData(props) {

  return (
    <div>
      <Dialog onClose={props.handleCloseNoData} aria-labelledby="customized-dialog-title" open={props.openNoData}>
        <DialogTitle id="customized-dialog-title" onClose={props.handleCloseNoData}>
          There is no heart rate data associated with the account!
        </DialogTitle>
        <DialogContent dividers>
          <Typography gutterBottom>
            There has not been any heart rates posted from you Apple Watch so far. If you just started
            it could take a couple of seconds for the first data to pop up.
          </Typography>
          <Typography gutterBottom>
            If you have not yet linked your Apple Watch to the iHeartRate application you can
            go to the apple app store to download the app! <Link>Apple App Store</Link>
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={props.handleCloseNoData} color="primary">
            I Understand
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}